<template>
  <div class="half-width" id="div1" @click="isMainClick()">
    <transition name="fade" mode="out-in">
      <img :src="landing_img_path" :key="landing_img_path" alt="">
    </transition>
    <transition name="slide">
      <!-- <div v-if="isMain" class="quard-width">details</div> -->
      <detailComponent v-if="isMain" class="quard-width"/>
    </transition>
  </div>
  <div class="half-width" id="div2">
    <introduceComponent />
  </div>
</template>

<script>
import introduceComponent from "@/components/introduceComponent.vue";
import detailComponent from "@/components/detailComponent.vue";
export default {
  data() {
    return {
      isMain: false
    };
  },
  computed: {
    landing_img_path() {
      return this.$store.state.landingPageImgSrc
    }
  },
  components: {
    introduceComponent,
    detailComponent
  },
  methods: {
    isMainClick(){
      if (this.isMain) {
        this.isMain=false
      } else {
        this.isMain=true
      }
    }
  }
}
</script>

