<template>
    <div class="content">
        <p>프로젝트명: {{ detailData.name }}</p>
        <p>프로젝트 요약: {{ detailData.content }}</p>
        <p>기간: {{ detailData.date }}</p>
        <p>스택: {{ detailData.stack }}</p>
        <p>역할:</p>
        <div v-for="(role, index) in detailData.role" :key="index">
            {{ role }}
        </div>
        <p>회고:</p>
        <div v-for="(review, index) in detailData.review" :key="index">
            <p>{{ review }}</p>
        </div>


    </div>
</template>

<script>
export default {
    computed: {
        detailData() {
            return this.$store.state.detailData
        }
    }
}
</script>

<style>

.content {
    font-size: 20px;
}
</style>